import React from 'react';
import './Contact.css';
import { connect } from 'react-redux';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faEnvelope, faPhoneAlt, faComments } from '@fortawesome/free-solid-svg-icons'

function Contact(props) {
    const { contacts } = props;

    const renderObj = {
        mail: (contactObj) => (
            <a href={`mailto:${contactObj.value}`} className="link">
                <FontAwesomeIcon style={{ margin: '0px 5px' }} icon={faEnvelope} />
                {contactObj.value}
            </a>
        ),
        cell: (contactObj) => (
            <a href={`tel:${contactObj.value}`} className="link">
                <FontAwesomeIcon style={{ margin: '0px 5px' }} icon={faPhoneAlt} />
                {contactObj.value}
            </a>
        ),
        whatsapp: (contactObj) => (
            <a href={contactObj.value} className="link" target="_blank">
                <FontAwesomeIcon style={{ margin: '0px 5px' }} icon={faComments} />
                {contactObj.number}
            </a>
        )
    }

    return (
        <div className="contactPage">
            <div className="pageTitle">
                Contact Me!
            </div>
            <div className="contactInfo">
                {
                    contacts.map((contactObj) => (
                        <div className="emailContainer row" key={contactObj.id}>
                            <div className="col-sm-3 col-md-3 col-xs-12"></div>
                            <div className="col-sm-3 col-md-3 col-xs-12">
                                <div className="contactTitle">
                                    <p>{contactObj.type === 'mail' ? 'Email' : (contactObj.type == 'cell' ? 'Phone' : 'Whatsapp')}</p>
                                </div>
                            </div>
                            <div className="col-sm-3 col-md-3 col-xs-12">
                                <div className="contactLink">
                                    {renderObj[contactObj.type](contactObj)}
                                </div>
                            </div>
                            <div className="col-sm-3 col-md-3 col-xs-12"></div>
                        </div>
                    ))
                }
            </div>
        </div>
    )
}
const mapStateToProps = (state) => ({
    contacts: state.contacts
})
export default connect(mapStateToProps)(Contact)